export function decimalToHexString(number) {
  if (number < 0) {
    number = 0xffffffff + number + 1;
  }

  return number.toString(16).toUpperCase();
}

const hex2rgb = function(hex) {
  const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
  return result
    ? [
        parseInt(result[1], 16),
        parseInt(result[2], 16),
        parseInt(result[3], 16)
      ]
    : null;
};

const rgb2hex = function(rgb) {
  return (
    "#" +
    ((1 << 24) + (rgb[0] << 16) + (rgb[1] << 8) + rgb[2]).toString(16).slice(1)
  );
};

// Interpolates two [r,g,b] colors and returns an [r,g,b] of the result
// Taken from the awesome ROT.js roguelike dev library at
// https://github.com/ondras/rot.js
const _interpolateColor = function(color1, color2, factor) {
  if (arguments.length < 3) {
    factor = 0.5;
  }
  let result = color1.slice();
  for (var i = 0; i < 3; i++) {
    result[i] = Math.round(result[i] + factor * (color2[i] - color1[i]));
  }
  return result;
};

const lerp = (start, end, amt) => {
  return (1 - amt) * start + amt * end;
};

const makeRange = (steps, max = 1, min = 0) => {
  return [...Array(steps)].map((v, i) => lerp(min, max, i / steps));
};

export const getColorOpacityRangeHex = (steps = 20, hex) => {
  const rgbIn = hex2rgb(hex);
  const faded = hex2rgb("#333333");

  const interpolated = makeRange(steps).map(v =>
    rgb2hex(_interpolateColor(rgbIn, faded, v))
  );

  return interpolated.reverse();
};
